import React, { useState, useEffect } from "react";
import styles from './LoadingDatabase.module.css';


const LoadingDatabase = props => {

    const [isLoading, setIsLoading] = useState(true);
    const [dataList, setDataList] = useState([]);

    useEffect(() => {
        setIsLoading(true);
        fetch(
           "https://react-project-1-eaa82-default-rtdb.firebaseio.com/products.json"
        )
           .then((response) => {
              return response.json();
           })
           .then((data) => {
              const realData = [];
              for (const key in data) {
                 const dataItem = {
                    id: key,
                    ...data[key],
                 };
    
                 realData.push(dataItem);
              }
    
              setIsLoading(false);
              setDataList(realData);
           });
     }, []);

     if (isLoading) {
        return (
           <section>
              <div className={styles.loading}>Loading db...</div>
           </section>
        );
     }

     props.dataBase(dataList);
     console.log(dataList, isLoading);

    return null;
}

export default LoadingDatabase;