import React, { useRef, useState } from "react";
import styles from "./Login.module.css";
import Button from "../UI/Button";
import Card from "../UI/Card";


const Signup = (props) => {
   const [error, setError]= useState('');
const username2Ref = useRef();
const password2Ref = useRef();
const emailRef = useRef();

   const switchHandler =()=> {
      props.onClick();
      console.log(password2Ref.current.value, emailRef.current.value)
   }

   const submitHandler = (event) => {
      event.preventDefault();
      setError('')
      console.log(password2Ref.current.value, emailRef.current.value)
      const enteredUsername = username2Ref.current.value;
      const enteredPassword = password2Ref.current.value;
      const enteredEmail = emailRef.current.value;

       const userData = {
          username: enteredUsername,
          password: enteredPassword,
          email: enteredEmail
       }

      fetch('https://identitytoolkit.googleapis.com/v1/accounts:signUp?key=AIzaSyC4_ps4hI1PjQgwx1YBfyZY2vcAZ_uMqxk',
      {
         method: 'POST',
         body: JSON.stringify({
            email: enteredEmail,
            password: enteredPassword,
            returnSecureToken: true
         }),
         headers: {
            'Content-Type': 'application/json'
         }
      }).then(res => {
         if (res.ok) {
               setError('User Created')
               fetch(
                  "https://react-project-1-eaa82-default-rtdb.firebaseio.com/users.json",
                  {
                     method: "POST",
                     body: JSON.stringify(userData),
                     headers: {
                        "Content-Type": "application/json",
                     },
                  }
               );
               // ....
         } else {
            return res.json().then(data => {
               // show error modal
               console.log(data.error.message);
               setError(data.error.message);
            })
         }
      });

      
      username2Ref.current.value = '';
   }
   return (
      <Card className={styles.card}>
         <h2>SIGN UP</h2>
         <form className={styles.form} onSubmit={submitHandler}>
         <label htmlFor="username2" className={styles.label}>
               Username:
            </label>
            <input id="username2" type="text" ref={username2Ref} required className={styles.input}></input>
            <label htmlFor="email" className={styles.label}>
               Email:
            </label>
            <input id="email" type="text" ref={emailRef} required className={styles.input}></input>
            <label htmlFor="password2" className={styles.label}>
               Password:
            </label>
            <input
               id="password2"
               type="password"
               ref={password2Ref} 
               required
               className={styles.input}
            ></input>
                     
            <Button type='submit' >Register</Button>
            <span>or</span>
            <Button onClick={switchHandler}>Cancel</Button>
         </form>
         <h4>{error}</h4>
      </Card>
   );
};

export default Signup;
