import React, { useContext } from "react";
import { NavLink } from "react-router-dom";
import CartContext from "../../store/cart-context";
import Button from "../UI/Button";
import Card from "../UI/Card";
import styles from "./ProdItem.module.css";

const ProdItem = (props) => {
   const cartCtx = useContext(CartContext);
   // console.log(cartCtx.items, cartCtx.totalAmount);

   const addToCartHandler = () => {
      cartCtx.addItem({
         key: Math.random(),
         id: props.id,
         name: props.prodName,
         type: props.prodType,
         price: +props.prodPrice,
      });
   };

   const typeClass = `${styles.type}
                      ${props.prodType === "Game" ? styles.Game : ""}
                      ${props.prodType === "Movie" ? styles.Movie : ""}
                      ${props.prodType === "Music" ? styles.Music : ""}
                      ${props.prodType === "App" ? styles.App : ""} `;

   return (
      <Card className={styles.card} key={props.id}>
         <div className={typeClass}></div>
         <div className={styles.name}>
            <NavLink className={styles.link} to={`/home/${props.id}`}>
               {props.prodName}
            </NavLink>
         </div>
         <div className={styles.price}>{`${props.prodPrice}$`}</div>
         <div className={styles.addButton}>
            <Button className={styles.button} onClick={addToCartHandler}>
               Add
            </Button>
         </div>
      </Card>
   );
};

export default ProdItem;
