import React, { useState } from "react";
import Button from "../UI/Button";
import Card from "../UI/Card";
import ErrorModal from "../UI/ErrorModal";
import styles from "./AddProd.module.css";

const AddProd = (props) => {
   const [prodType, setProdType] = useState("");
   const [prodName, setProdName] = useState("");
   const [prodPrice, setProdPrice] = useState("");
   const [prodDescription, setProdDescription] = useState("");
   const [error, setError] = useState();

   const infoHandler1 = (event) => {
      setProdType(event.target.value);
   };
   const infoHandler2 = (event) => {
      setProdName(event.target.value);
   };
   const infoHandler3 = (event) => {
      setProdPrice(event.target.value);
   };
   const infoHandler4 = (event) => {
      setProdDescription(event.target.value);
   };

   const formHandler = (event) => {
      event.preventDefault();

      if (prodType.trim().length === 0) {
         setError({
            title: "Product Type is empty",
            message: "Select product type",
         });
         return;
      }

      if (prodName.trim().length === 0) {
         setError({
            title: "Product Name is empty",
            message: "Type product name",
         });
         return;
      }
      if (prodPrice.trim().length === 0) {
         setError({
            title: "Product price is empty",
            message: "Type product price",
         });
         return;
      }
      if (prodDescription.trim().length === 0) {
         setError({
            title: "Product description is empty",
            message: "Type product description",
         });
         return;
      }

      // props.onAddProd(prodType, prodName, prodPrice, prodDescription);
      const ProdData = {
         prodType: prodType,
         prodName: prodName,
         prodPrice: prodPrice,
         prodDescription: prodDescription,
      };

      fetch(
         "https://react-project-1-eaa82-default-rtdb.firebaseio.com/products.json",
         {
            method: "POST",
            body: JSON.stringify(ProdData),
            headers: {
               "Content-Type": "application/json",
            },
         }
      );

      
      props.onClick();
      setProdType("");
      setProdName("");
      setProdPrice("");
      setProdDescription("");
   };

   const errorHandler = () => {
      setError(null);
   };

   return (
      <div>
         {error && (
            <ErrorModal
               title={error.title}
               message={error.message}
               onClick={errorHandler}
            />
         )}

         <Card className={styles.card}>
            <form className={styles.form} onSubmit={formHandler}>
               <h3>Add new product</h3>
               <label htmlFor="prodType">Type:</label>
               <select
                  id="prodType"
                  type="text"
                  value={prodType}
                  onChange={infoHandler1}
               >
                  <option value="empty">-</option>
                  <option value="Game">Game</option>
                  <option value="Movie">Movie</option>
                  <option value="Music">Music</option>
                  <option value="App">App</option>
               </select>
               <label htmlFor="prodName">Name:</label>
               <input
                  id="prodName"
                  type="text"
                  value={prodName}
                  onChange={infoHandler2}
               />
               <label htmlFor="prodPrice">Price:</label>
               <input
                  id="prodPrice"
                  type="number"
                  min="0"
                  value={prodPrice}
                  onChange={infoHandler3}
               />
               <label htmlFor="prodDescription">Description:</label>
               <textarea
                  id="prodDescription"
                  type="text"
                  value={prodDescription}
                  onChange={infoHandler4}
               />
               <Button type="submit">Add</Button>
               <Button type="button" onClick={props.onClick}>
                  Cancel
               </Button>
            </form>
         </Card>
      </div>
   );
};

export default AddProd;
