import React, { useState } from "react";
import Button from "./Button";
import Card from "./Card";
import styles from "./NavigationBar.module.css";

const NavigationBar = (props) => {

   const [search,setSearch]=useState('');

   const clickHandler2=()=> {
       props.onClick('Game');
     
   }
   const clickHandler3=()=> {
       props.onClick('Movie');
      
   }
   const clickHandler4=()=> {
       props.onClick('Music');
       
   }
   const clickHandler5=()=> {
       props.onClick('App');
       
   }
   

   const searchInputHandler=(e)=>{
      setSearch(e.target.value);
   }

   const searchHandler=()=> {
      props.searchButton(search);
      setSearch('');
      
   }

   return (
      <Card className={styles.card}>
         <div className={styles.nav1}>
            
            <div>
               <Button onClick={searchHandler}>All</Button>
            </div>
            <div>
               <Button onClick={clickHandler2}>Games</Button>
            </div>
            <div>
               <Button onClick={clickHandler3}>Movies</Button>
            </div>
            <div>
               <Button onClick={clickHandler4}>Music</Button>
            </div>
            <div>
               <Button onClick={clickHandler5}>Apps</Button>
            </div>
         </div>
         <div className={styles.nav2}>
            <input className={styles.input} onChange={searchInputHandler} value={search}/>
            <Button className={styles.searchButton} onClick={searchHandler}>Search by name</Button>
         </div>
      </Card>
   );
};

export default NavigationBar;
