import React, { useContext, useState } from "react";

import Cart from "./components/Cart/Cart";
import Footer from "./components/Layout/Footer";
import Header from "./components/Layout/Header";
import Main from "./components/Layout/Main";
import Login from "./components/Login/Login";
import Signup from "./components/Login/Signup";
import AddNewProdButton from "./components/Product/AddNewProdButton";
import AddProd from "./components/Product/AddProd";

import CardBox from "./components/UI/CardBox";
import NavigationBar from "./components/UI/NavigationBar";
import { Route, Switch, Redirect } from "react-router-dom";
import ProductDetail from "./components/Product/ProductDetail";

import CartProvider from "./store/CartProvider";
import ProdListNew from "./components/Product/ProdListNew";
import LoadingDatabase from "./components/Product/LoadingDatabase";
import AuthContext from "./store/auth-context";
import styles from "./index.module.css";

function App() {
   const [selectedType, setSelectedType] = useState("name");
   const [searchedItem, setSearchedItem] = useState("");
   const [formState, setFormState] = useState("open");
   const [dataBase, setDataBase] = useState([]);
   const [switch1, setSwitch1] = useState(false);
   const [dbReload, setDbReload] = useState(true);

   const addFormHandler = () => {
      if (formState === "open") {
         setFormState("close");
      } else {
         setFormState("open");
      }
      console.log(formState);
   };

   const switch1Handler = () => {
      console.log(switch1);
      setSwitch1(!switch1);
   };

   const dbReloadHandler = () => {
      console.log('login Handler')
      setDbReload(false);
   };

   const sortByTypeHandler = (search) => {
      setSelectedType("type");
      setSearchedItem(search);
   };

   const searchedItemHandler = (search) => {
      setSelectedType("name");
      setSearchedItem(search);
   };

   const databaseHandler = (db) => {
      setDataBase(db);
      setTimeout(()=>{setDbReload(false)},1000)
      
   };

   const authCtx = useContext(AuthContext);
   const isLoggedIn = authCtx.isLoggedIn;

   console.log('loggedin:',isLoggedIn , 'admin:', authCtx.admin, 'dbReload', dbReload);

   return (
      <CartProvider>
         <Switch>
            <Route path="/" exact>
               <Redirect to="/login" />
            </Route>
            <Route path="/login">
               <CardBox>
                  <Header />
                  {!isLoggedIn && (
                     <Main className={styles.main}>
                        {!switch1 && <Login onClick={switch1Handler} dbReload={dbReloadHandler} />}
                        {switch1 && <Signup onClick={switch1Handler} />}
                        <LoadingDatabase dataBase={databaseHandler} />
                     </Main>
                  )}
                  <Footer />
               </CardBox>
            </Route>

            <Route path="/home" exact>
               <CardBox>
                  <Header />
                  {isLoggedIn && (
                     <Main>
                        {formState === "open" && (
                           <>
                              <NavigationBar
                                 onClick={sortByTypeHandler}
                                 searchButton={searchedItemHandler}
                              />
                              {authCtx.admin === true &&<AddNewProdButton onClick={addFormHandler} />}
                              {dbReload && <LoadingDatabase dataBase={databaseHandler} />}
                              <ProdListNew
                                 sortingBy={selectedType}
                                 searchBy={searchedItem}
                                 dataBase={dataBase}
                              />
                              
                           </>
                        )}
                        {formState === "close"  && (
                           <AddProd onClick={addFormHandler} />
                        )}
                     </Main>
                  )}
                  <Footer />
               </CardBox>
            </Route>
            <Route path="/cart">
               <CardBox>
                  <Header />
                  {isLoggedIn && (
                     <Main>
                        <Cart products={dataBase} />
                     </Main>
                  )}
                  <Footer />
               </CardBox>
            </Route>
            <Route path="/home/:productId">
               <CardBox>
                  <Header />
                  <Main>
                     <ProductDetail products={dataBase} />
                  </Main>
                  <Footer />
               </CardBox>
            </Route>
            <Route path="*" exact>
               <Redirect to="/" />
            </Route>
         </Switch>
      </CartProvider>
   );
}

export default App;
