import React, { useContext, useRef, useState } from "react";
import { useHistory } from "react-router";
import AuthContext from "../../store/auth-context";
import Button from "../UI/Button";
import Card from "../UI/Card";
import styles from "./Login.module.css";

const Login = (props) => {
   const password1Ref = useRef();
   const email1Ref = useRef();
   const [error, setError]= useState('');
   const authCtx = useContext(AuthContext);
   const history = useHistory();
   const [isLoading, setIsLoading] = useState(false);

   const switchHandler =()=> {
      props.onClick();
   }

   const dbReloadHandler=()=>{
      props.dbReload();
   }

   const submitHandler = (event) => {
      event.preventDefault();
      setError('')
      console.log(password1Ref.current.value, email1Ref.current.value)

      const enteredPassword = password1Ref.current.value;
      const enteredEmail = email1Ref.current.value;


      setIsLoading(true);
      fetch('https://identitytoolkit.googleapis.com/v1/accounts:signInWithPassword?key=AIzaSyC4_ps4hI1PjQgwx1YBfyZY2vcAZ_uMqxk',
      {
         method: 'POST',
         body: JSON.stringify({
            email: enteredEmail,
            password: enteredPassword,
            returnSecureToken: true
         }),
         headers: {
            'Content-Type': 'application/json'
         }
      }).then(res => {
         if (res.ok) {
            
            return  res.json().then(data=>{
               const expirationTime = new Date(new Date().getTime()+(+data.expiresIn * 1000));
               console.log(data);
               authCtx.login(data.idToken, expirationTime.toString(), data.email);
               history.replace('/home');
               setError('User Loged In')
               setIsLoading(false);
            })

               // ....
         } else {

            return res.json().then(data => {
               // show error modal
               console.log(data.error.message);
               setError(data.error.message);
               setIsLoading(false);
            })
         }
      });

      if (isLoading) {
         return (
            <section>
               <div className={styles.loading}>Loading...</div>
            </section>
         );
      }

      
   }

   return (
      <Card className={styles.card}>
         <h2>LOGIN</h2>
         <form className={styles.form} onSubmit={submitHandler}>
            <label htmlFor="email1" className={styles.label}>
               Email:
            </label>
            <input id="email1" type="text" className={styles.input} ref={email1Ref} />
            <label htmlFor="password1" className={styles.label}> 
               Password:
            </label>
            <input
               id="password1"
               type="password"
               ref={password1Ref}
               className={styles.input}
            />
            <Button type='submit' onClick={dbReloadHandler}>Login</Button>
            <span>or</span>
            <Button onClick={switchHandler}>Sign Up</Button>
         </form>
         {isLoading &&  <h4 className={styles.loading}>Loading...</h4>}
         <h4>{error}</h4>
      </Card>
   );
};

export default Login;
