
import React, { useState } from "react";

let logoutTimer;

const AuthContext = React.createContext({
    token: '',
    isLoggedIn : false,
    admin: false,
    login: (token) => {},
    logout: (token)=> {}
});

const calculateRemainingTime = (expirationTime) => {
    const currentTime = new Date().getTime();
    const adjEpirationTime = new Date(expirationTime).getTime();

    const remainingDuration = adjEpirationTime - currentTime;
    return remainingDuration;
}



export const AuthContextProvider =(props)=> {
    const initialToken = localStorage.getItem('token');
    const [token, setToken] = useState(initialToken);
    const [admin, setAdmin] = useState(false);

    const userIsLoggedIn = !!token; // if token empty => false, esle => true
    


    const logoutHandler =()=>{
        setToken(null);
        setAdmin(false);
        localStorage.removeItem('token');

        if (logoutTimer) {
            clearTimeout(logoutTimer);
        }
    };
    
    const loginHandler = (token, expirationTime, email)=>{
        setToken(token);
        if ( email === 'mariussaudaniel@gmail.com') {
            setAdmin(true);
        }
        localStorage.setItem('token', token);
        localStorage.setItem('expirationTime', expirationTime);

        const remainingTime = calculateRemainingTime(expirationTime);
        logoutTimer = setTimeout(logoutHandler, remainingTime);
    };

    const contextValue = {
        token: token,
        isLoggedIn: userIsLoggedIn,
        admin: admin,
        login: loginHandler,
        logout: logoutHandler
    }
    
    //  console.log(token);
    return <AuthContext.Provider value={contextValue}>{props.children}</AuthContext.Provider>
}

export default AuthContext;