import React, { useContext } from "react";
import styles from "./Header.module.css";
import { Link } from "react-router-dom";
import CartContext from "../../store/cart-context";
import { useEffect, useState } from "react/cjs/react.development";
import AuthContext from "../../store/auth-context";

const Header = (props) => {
   const cartCtx = useContext(CartContext);
   const authCtx = useContext(AuthContext);
   const [isOn, setIsOn] = useState(false);
   const animClass = `${styles.cartItems} ${isOn ? styles.bump : ""}`;
   

   const isLoggedIn = authCtx.isLoggedIn;

   useEffect(() => {
      if (cartCtx.itemsInCart) {
         setIsOn(true);
      }
      const timer = setTimeout(() => {
         setIsOn(false);
      }, [300]);
      return () => {
         clearTimeout(timer);
      };
   }, [cartCtx.itemsInCart]);

   const logoutHandler =()=>{
      authCtx.logout()
   }

   return (
      <div>
         {isLoggedIn && (
            <div className={styles.header}>
               <Link to="/home" className={styles.home}></Link>
               <div className={styles.cartContainer}>
                  <Link to="/cart" className={styles.cart}></Link>
                  <div className={animClass}>{cartCtx.itemsInCart}</div>
               </div>
               <Link to="/login" className={styles.logout} onClick={logoutHandler}> </Link>
            </div>
         )}
         {!isLoggedIn && (
            <div className={styles.header1}>
               <Link to="/login" className={styles.login}></Link>
            </div>
         )}
      </div>
   );
};

export default Header;
