import React from "react";
import styles from "./Cart.module.css";
import Card from "../UI/Card";
import Button from "../UI/Button";
import { useContext } from "react";
import CartContext from "../../store/cart-context";

const CartItem = (props) => {
    const cartCtx = useContext(CartContext);
    const removeFromCartHandler = () => {
        cartCtx.removeItem(props.keyID);
     };

     const typeClass = `${styles.type}
                      ${props.type === "Game" ? styles.Game : ""}
                      ${props.type === "Movie" ? styles.Movie : ""}
                      ${props.type === "Music" ? styles.Music : ""}
                      ${props.type === "App" ? styles.App : ""} `;
   return (
      <Card className={styles.cardMain} key={props.keyID}>
         <div className={styles.name}>{props.name}</div>
         <div className={typeClass}></div>
         <div className={styles.price}>{`${props.price}$`}</div>
         <div className={styles.addButton}>
            <Button className={styles.button} onClick={removeFromCartHandler}>
               Remove
            </Button>
         </div>
      </Card>
   );
};

export default CartItem;
