import React from "react";

import { useState } from "react";
import Card from "../UI/Card";
import { useEffect } from "react/cjs/react.development";
import ProdItem from "./ProdItem";

const ProdListNew = (props) => {

   const [sortedList, setSortedList] = useState([]);
   const dataList = props.dataBase;
   
   useEffect(() => {
      
      console.log(dataList);

      const newList = dataList.filter((prod) => {
         if (props.sortingBy === "type") {
            return prod.prodType === props.searchBy;
         } else if (props.sortingBy === "name") {
            
            return (prod.prodName).toLowerCase().includes(props.searchBy.toLowerCase());
               
               
         } else {
            // console.log('sortingType', props.searchBy, dataList)
            return console.log("eroare la sortare", props.searchBy);
         }
      });

      
      setSortedList(newList);
      console.log(newList);
   }, [dataList , props.searchBy, props.sortingBy]);


   return (
      <Card>
         {sortedList.map((prod) => (
            <div key={Math.random()}>
               <ProdItem
                  id={prod.id}
                  prodType={prod.prodType}
                  prodName={prod.prodName}
                  prodPrice={prod.prodPrice}
               />
            </div>
         ))}
      </Card>
   );
};

export default ProdListNew;
