import React, { useContext} from "react";

import Button from "../UI/Button";
import Card from "../UI/Card";
import styles from "./Cart.module.css";
import CartContext from "../../store/cart-context";
import CartItem from "./CartItem";

const Cart = props => {

   
   const cartCtx = useContext(CartContext);
   console.log(cartCtx.items, cartCtx.totalAmount);
   

 
   
   return (
      <Card className={styles.card}>
         <Card className={styles.cardHeader}>
            <div className={styles.name}>Name</div>
            <div className={styles.type}>Type</div>
            <div className={styles.price}>Price</div>
            <div className={styles.addButton} />
           
         </Card>
         {cartCtx.items.map((prod) => (
            <div key={Math.random()}>
            <CartItem id={prod.id} name={prod.name} type={prod.type} price={prod.price} keyID={prod.key} /></div>
         ))}
         <Card className={styles.cardFooter}>
            <div className={styles.total}>Total</div>
            <div className={styles.price}>{`${cartCtx.totalAmount} $`}</div>
            <div className={styles.addButton}>
               <Button className={styles.button}>Buy</Button>
            </div>
         </Card>
      </Card>
   );
};

export default Cart;
